/* ===== Scrollbar CSS ===== */

html {
    overflow: overlay;
}

/* Text Selection */
::-moz-selection {
    background: $color_secondary_87;
    color: $color_neutral_10;
}

::selection {
    background: $color_secondary_87;
    color: $color_neutral_10;
}

/* Animation */
.animation-delay-0 {
    animation-delay: 0!important;
} 
.animation-delay-100 {
    animation-delay: 100ms!important;
} 
.animation-delay-200 {
    animation-delay: 200ms!important;
} 
.animation-delay-300 {
    animation-delay: 300ms!important;
} 
.animation-delay-400 {
    animation-delay: 400ms!important;
} 

.animation-fadeFromRight {
    animation: fadeOutLeft 700ms ease-in-out;
}

.animation-fadeFromLeft {
    animation: fadeOutRight 700ms ease-in-out;
}

.animation-fadeToRight {
    animation: fadeInLeft 700ms ease-in-out;
}

.animation-fadeToLeft {
    animation: fadeInRight 700ms ease-in-out;
}

.animation-fadeInUp {
    animation: fadeInUp 1s ease-in-out; 
}

.animation-zooming {
    animation: zooming 500ms linear;
}

.animation-ScrollDown {
    animation: scrollDown 1500ms linear;
}

.animation-ScrollUp {
    animation: ScrollUp 1500ms linear;
}

.ripple {
    width: 140px;
    height: 140px;
    border-radius: 140px;
    position: absolute;
    opacity: 0;
    margin-top: -70px;
    margin-left: -70px;
    animation-name: pulse;
    -webkit-animation-name: pulse;
    -moz-animation-name: pulse;
    -o-animation-name: pulse;
    animation-duration: 1100ms;
    -webkit-animation-duration: 1100ms;
    -moz-animation-duration: 1100ms;
    -o-animation-duration: 1100ms;
    animation-timing-function: cubic-bezier(0.165, 0.840, 0.440, 1.000);
    -webkit-animation-timing-function: cubic-bezier(0.165, 0.840, 0.440, 1.000);
    -moz-animation-timing-function: cubic-bezier(0.165, 0.840, 0.440, 1.000);
    -o-animation-timing-function: cubic-bezier(0.165, 0.840, 0.440, 1.000);
    animation-iteration-count: 1;
    -webkit-animation-iteration-count: 1;
    -moz-animation-iteration-count: 1;
    -o-animation-iteration-count: 1;
    will-change: opacity, transform, border;
    border-style: solid;
    border-width: 20px;
    border-color: #fff;
}

.btn-default > .ripple{
    border-color: $color_primary_50;
}

.concept-b:not(.mode-dark) .btn-default > .ripple,
.panel-cards-trendy:not(.mode-dark) .btn-default > .ripple{
    border-color: $color_neutral_40;
}

.mode-dark .btn-default > .ripple{
    border-color: #fff;
}

@keyframes pulse {
0% {transform: scale(0);  -moz-transform: scale(0); -o-transform: scale(0); opacity: 0; border-width: 20px;}
3% {opacity: 0.6;}
100% {transform: scale(1.5); -webkit-transform: scale(1.5); -moz-transform: scale(1.5); -o-transform: scale(1.5); opacity: 0; border-width: 0px;}
}

@-webkit-keyframes pulse {
0% {transform: scale(0);  -moz-transform: scale(0); -o-transform: scale(0); opacity: 0; border-width: 20px;}
3% {opacity: 0.6;}
100% {transform: scale(1.5); -webkit-transform: scale(1.5); -moz-transform: scale(1.5); -o-transform: scale(1.5); opacity: 0; border-width: 0px;}
}

@-moz-keyframes pulse {
0% {transform: scale(0);  -moz-transform: scale(0); -o-transform: scale(0); opacity: 0; border-width: 20px;}
3% {opacity: 0.6;}
100% {transform: scale(1.5); -webkit-transform: scale(1.5); -moz-transform: scale(1.5); -o-transform: scale(1.5); opacity: 0; border-width: 0px;}
}

@-o-keyframes pulse {
0% {transform: scale(0);  -moz-transform: scale(0); -o-transform: scale(0); opacity: 0; border-width: 20px;}
3% {opacity: 0.6;}
100% {transform: scale(1.5); -webkit-transform: scale(1.5); -moz-transform: scale(1.5); -o-transform: scale(1.5); opacity: 0; border-width: 0px;}
}


@keyframes scrollDown {
    0% {
        top: -65px
    }

    100% {
        top: 0;
    }
}

@keyframes ScrollUp {
    0% {
        top: 0;
    }

    100% {
        top: -65px
    }
}

@keyframes zooming {
    0% {
        scale: 0.97;
        opacity: 0.85;
        transform: translateY(12px);
    }

    50% {
        scale: 0.985;
        opacity: 1;
    }

    100% {
        scale: 1;
        opacity: 1;
        transform: translateY(0px);
    }
}


@keyframes fadeOutLeft {
    0% {
        transform: translateX(0);
        opacity: 1;
    }

    100% {
        transform: translateX(-700px);
        opacity: 0;
    }
}

@keyframes fadeOutRight {
    0% {
        transform: translateX(0);
        opacity: 1;
    }

    100% {
        transform: translateX(700px);
        opacity: 0;
    }
}

@keyframes fadeInLeft {
    0% {
        transform: translateX(-700px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fadeInRight {
    0% {
        transform: translateX(700px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}


  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes enlargeWidth {
