/* Animation abstract*/
.animation-objgroup {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*overflow: hidden;*/
}

/* Animation abstract 01 */
.animation-objgroup.animation-01-objgroup span {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(57, 65, 70, 0.3);
    animation:  animate-move-01 25s linear infinite, animate-opacity 25s linear infinite, animate-radius 25s linear infinite;
    bottom: -150px;
}

.animation-objgroup.animation-01-objgroup span:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation:  animate-move-01 linear infinite, animate-opacity linear infinite, animate-radius linear infinite;
    animation-delay: 3s;
    animation-duration: 25s; 
}  

.animation-objgroup.animation-01-objgroup span:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;   
    animation:  animate-move-02 linear infinite, animate-opacity linear infinite, animate-radius linear infinite;
    animation-delay: 2s;
    animation-duration: 12s; 
}  

.animation-objgroup.animation-01-objgroup span:nth-child(3) {
    left: 35%;
    width: 60px;
    height: 60px;
    background-color: transparent;
    border: 4px solid rgba(57, 65, 70, 0.3);
