    animation-delay: 4s;
    animation-duration: 20s; 
}

.animation-objgroup.animation-01-objgroup span:nth-child(4) {
    left: 40%;
    width: 0;
    height: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
