}

.form-range input[type=range]:focus::-moz-range-thumb {
    box-shadow: 0 0 0 1px #fff, 0 0 0 0.25rem $color_outline_onlight;
}



/* Seo - Accessibility ---- Gestione link rapidi */

#skiplink{
    position: absolute;
    white-space: nowrap;
}

.sr-only-focusable-abs {
    z-index: -1;
    position: absolute;
    top: 10px;
    margin-left: 0.438rem;
    padding: 0.438rem 0.5rem 0.438rem 0.5rem;
    background-color: #fff;
    border: 1px solid $color_neutral_30;
    border-radius: 3px;
    opacity: 0;
}

.sr-only-focusable-abs:focus {
